import Image from "."
import './index.css'

interface IProps {
    src: string
    width?: number
    height?: number
    style?: React.CSSProperties
    className?: string
    infinite?: boolean
    onClick?: () => void
}

export default function AnimateBg (props: IProps) {
    const { infinite, ...others } = props
    return (
        <div className={`mobile-bgimg ${infinite ? 'infinite' : ''}`}>
            <Image {...others} />
        </div>
    )
}