import { useEffect, useState } from "react"
import dayjs from "dayjs"

export type NewOrder = {
    date: string        // 
    days: number        // 
    adults: number      // 
    children: number    // 
    name: string        // 
    phone: string       // 
    email: string       // 
    notes: string       // 
}

export type Order = {
    id: number
    addDate: string
    processed: boolean  // 
} & NewOrder

function formatDate (date: string, format = 'YYYY/MM/DD') {
    return dayjs(date).format(format)
}

function OrderItem ({ data, refetch }: { data: Order, refetch: () => void }) {
    const {
        date,
        days,
        adults,
        children,
        name,
        phone,
        email,
        notes,
        processed,
        addDate
    } = data

    const [iprocessed, setIProcessed] = useState<boolean>(processed)
    const handleChangeProcessed = () => {
        fetch('/api/updateOrder', {
            method: 'post',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({
                ...data,
                processed: !iprocessed
            })
        }).then(res => res.json()).then(res => {
            if (res.code === 0) {
                setIProcessed(!iprocessed)
                refetch()
            } else {
                alert(res.message)
            }
        })
    }

    useEffect(() => {
        setIProcessed(processed)
    }, [processed])

    return (
        <div className="order-item">
            <div className="order-caption">
                <label className="checkbox"><input type="checkbox" checked={iprocessed} onChange={handleChangeProcessed} /> </label>
                <div className="order-date">{addDate}</div>
            </div>
            <div className="order-basic">
                <div className="order-info">
                    <div className="order-attr">
                        <div className="os-cpt"></div>
                        <div>{formatDate(date)} - {formatDate(dayjs(date).add(days, 'day').toString())}</div>
                    </div>
                    <div className="order-attr">
                        <div className="os-cpt"></div>
                        <div>{days+1} {days}</div>
                    </div>
                    <div className="order-attr">
                        <div className="os-cpt"> </div>
                        <div>{adults} </div>
                    </div>
                    <div className="order-attr">
                        <div className="os-cpt"> </div>
                        <div>{children} </div>
                    </div>
                </div>
                <div className="order-contact">
                    <div className="order-attr">
                        <div className="os-cpt"></div>
                        <div>{name}</div>
                    </div>
                    <div className="order-attr">
                        <div className="os-cpt"></div>
                        <div>{phone}</div>
                    </div>
                    <div className="order-attr">
                        <div className="os-cpt"></div>
                        <div>{email || '--'}</div>
                    </div>
                </div>
                <div className="order-notes">
                    <div className="os-cpt"></div>
                    <div>{notes || '--'}</div>
                </div>
            </div>
        </div>
    )
}

export default function Orders () {
    const [orders, setOrders] = useState<Order[]>([])
    const [hide, setHide] = useState(false)

    const fetchOrders = () => {
        fetch('/api/getOrders').then(res => res.json()).then(res => {
            setOrders(res.data)
        }).catch(err => {
            alert('')
        })
    }

    useEffect(() => {
        fetchOrders()
    }, [])

    return (
        <div className="orders">
            <label className="checkbox"><input type="checkbox" checked={hide} onChange={() => setHide(!hide)} /> </label>
            <div className="order-list">
                { orders
                    .filter(o => hide ? !o.processed : true)
                    .sort((a, b) => dayjs(b.addDate).valueOf() - dayjs(a.addDate).valueOf())
                    .map((order: Order) => (
                        <OrderItem data={order} refetch={fetchOrders} />
                    ))
                }
            </div>
        </div>
    )
}
